import React from 'react'
import {Link} from "gatsby"

const NavLink=props=>(
    <li style={{
      display:`inline-block`,
      padding:`0 1rem`
      }}
    >
        <Link style={{
                lineHeight:`42px`
            }}
            to={props.to}
        >
            {props.children}
        </Link>
    </li>
)

export default props => (
    <div style={{
        maxWidth:1024,
        margin:`3rem auto`,
        color:`white`
    }}>
        <header>
            <Link to='/'>
                <h1 style={{display:`inline-block`}}>BADDPOOL</h1>
            </Link>
            <ul style={{
                float:`right`,
                listStyle:`none`
            }}>
                <NavLink to='/'>Home</NavLink>
                <NavLink to='/album'>Album</NavLink>
                <NavLink to='/contact'>Contact</NavLink>
                <NavLink to='/about'>About</NavLink>
            </ul>
        </header>
        

        {props.children}
    </div>
)
